import React from 'react';
import { useSelector, useDispatch } from "react-redux";

import { Link } from 'react-router-dom';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faBars } from '@fortawesome/free-solid-svg-icons';
import styled from 'styled-components';


function Menubar() {
    // dispatchÎ•º ÏÇ¨Ïö©ÌïòÍ∏∞ ÏúÑÌïú Ï§ÄÎπÑ
    const dispatch = useDispatch();

    // isMenuÍ∞Ä Ìï®ÏàòÏª¥Ìè¨ÎÑåÌä∏ ÏïàÏóê ÏûàÏñ¥ÏÑú Ï†ÑÏó≠Ïùò styled-componentÍ∞Ä ÏÇ¨Ïö©ÌïòÏßÄ Î™ªÌïòÎäî Ï§ëÏûÑ
    const _isMenu = useSelector(state => {
        return state.reducerMenu.menu;
    });

    return (
        <StateBar >
            <StateBarColumn style={{ paddingTop: '5px' }}>
                <FontAwesomeIcon icon={faBars}
                    size="2x"
                    onClick={() => {
                        if (!_isMenu) {
                            dispatch({ type: 'OPEN_MENU' })
                        } else {
                            dispatch({ type: 'CLOSE_MENU' })
                        }
                    }} /></StateBarColumn>
            <StateBarColumn
                onClick={() => {
                    dispatch({ type: 'CLOSE_MENU' });
                    return;
                }}
            ><Link to='/' style={{ textDecoration: 'none', color: 'black', fontSize: '35px' }}
            >Hachaerin</Link></StateBarColumn>

        </StateBar>
    )
}


